      .col-lg-8(data-aos="fade-left")
        p La ciberseguridad toma cada da mayor fortaleza gracias al creciente nmero de incidentes que sufren las empresas a causa de los ataques asociados a los sistemas y a la informacin.  Esto ha hecho que recurra a controles para garantizar la seguridad de los dispositivos informticos, activos de informacin y redes de comunicaciones, entre otros.  A partir de esta serie de nuevos requisitos se empieza a hablar del concepto de ciberseguridad.
        p Realizar un control eficaz a travs de la implementacin de un plan de ciberseguridad no es tarea sencilla, considerando el reto de enfrentarse a grandes cantidades de equipos, nuevas tecnologas, ciberdelincuencia en bsqueda permanente de nuevos ataques que allanen las vulnerabilidades de los sistemas. A pesar de esto, se puede pensar en la implementacin de medidas para la proteccin de la informacin a travs de procedimientos basados en estndares establecidos para temas de seguridad informtica.

    .row.justify-content-center.align-items-center
      .col-lg-9.mb-4.mb-lg-0(data-aos="fade-left")
        p La Organizacin Internacional de Normalizacin (ISO) y la Comisin Electrnica Internacional (IEC) referentes en la normalizacin a nivel mundial, en conjunto construyen comits tcnicos encargados de la elaboracin de normativas internacionales que son redactadas con el objetivo de realizar la regulacin de procesos especficos en todo lo que tiene que ver con la seguridad de la informacin.  Hoy en da estos estndares y normas juegan un papel importante, instando a las organizaciones a adelantar su implementacin, con altos niveles de cumplimiento, a travs de lo cual adquieren prestigio y reconocimiento.  El valor agregado que aporta este tipo de certificaciones es fundamental, haciendo que clientes y accionistas vean con buenos ojos a la organizacin y depositen en ella su confianza.
        .bg_10.p-4
          p.m-0 Las normas establecidas por la ISO estn numeradas de manera incremental acorde con su objetivo, a su vez se dividen en familias que agrupan temticas afines.  El principal objetivo de estas normas y estndares yace en la identificacin de nuevas y mejores tcnicas, guas, capacitaciones, polticas, entre otras encaminadas a un propsito especfico, por ejemplo, seguridad, calidad o escalabilidad.
      .col-lg-3.mb-4.mb-lg-3(data-aos="fade-right")
        figure
          img.img-t.m-auto(src='@/assets/curso/tema2/img02.svg' alt="")

    Separador

    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="zoom-out-right")
      h2 2.1 ISO 27001

    p Conocida como la Norma Tecnologa de la informacin  Tcnicas de seguridad  Sistemas de gestin de la seguridad de la informacin  Requisitos ISO/IEC 27001 (en lo sucesivo, ISO/IEC 27001) especifica los requisitos para establecer, implementar, mantener y mejorar continuamente un sistema de gestin de seguridad de la informacin (SGSI). El SGSI presenta un enfoque sistemtico para mantener segura la informacin confidencial y gestiona personas, procesos y sistemas de TI mediante la aplicacin de procesos de gestin de riesgos. El SGSI de una organizacin certificado segn la Norma ISO/IEC 27001 demuestra el compromiso de una organizacin con la seguridad de la informacin y brinda confianza a sus clientes, socios y partes interesadas.
    p.mb-5 ISO/IEC 27001 est diseada para usarse junto con controles de apoyo, como los relacionados en el documento ISO/IEC 27002 (en lo sucesivo, ISO/IEC 27002) que detalla 114 controles de seguridad organizados en 14 secciones y 35 objetivos de control.

    .btn-h3.mb-4
      span.h3 Requisitos de certificacin

    .row.mb-4
      .col-lg-8(data-aos="fade-right")
        p Para cumplir con los requisitos de la certificacin ISO/IEC 27001, el SGSI de una organizacin debe ser auditado por un organismo de certificacin acreditado internacionalmente. Los requisitos de las secciones 4 a 10 de ISO/IEC 27001 son obligatorios y no se permiten exclusiones.  Una vez superada la auditora formal, el organismo de certificacin otorga a la organizacin un certificado ISO/IEC 27001 por su SGSI vlido por 3 aos, luego de lo cual el SGSI debe volver a certificarse.
        p Durante el perodo de validez de 3 aos, la organizacin debe realizar el mantenimiento del certificado para confirmar que el SGSI sigue cumpliendo, opera segn lo especificado y mejora continuamente. Para mantener la certificacin, el organismo de certificacin visitar el sitio del SGSI al menos una vez al ao para realizar una auditora de vigilancia. Durante la auditora de vigilancia solo se auditar una parte del SGSI. Hacia el final del perodo de 3 aos el organismo de certificacin audita todo el SGSI.
      .col-lg-4(data-aos="fade-left")
        figure
          img.img-t.m-auto(src='@/assets/curso/tema2/img04.png' alt="")

    .btn-h3.mb-4
      span.h3 Beneficios de la certificacin

    p.mb-5 La certificacin ISO/IEC 27001 traer beneficios a la organizacin tanto en seguridad interna como en competitividad externa.  Internamente al adoptar ISO/IEC 27001 una organizacin puede beneficiarse entre otros aspectos, de los relacionados en la siguiente figura.

