          p El proceso de certificacin <b>ISO/IEC 27001</b> implica la acreditacin por parte de organismos de certificacin. Dicha acreditacin se otorga a las organizaciones que han demostrado cumplir plenamente con los requisitos de las normas internacionales <b>ISO/IEC 17021</b> Evaluacin de la conformidad: requisitos para los organismos que brindan auditora y certificacin de sistemas de gestin e <b>ISO/IEC 27006</b> Requisitos para los organismos que brindan auditora y certificacin de sistemas de gestin de seguridad de la informacin.
