    .btn-h3.mb-4
      span.h3 Acreditacin servicio por ISO/CEI 27001

    p.mb-3.mb-lg-5 La certificacin fue lanzada oficialmente por el Servicio de Acreditacin de Hong Kong (HKAS) el 15 de noviembre de 2011. Los organismos de certificacin pueden comunicarse con HKAS y solicitar la acreditacin de forma voluntaria.

    .row.align-items-center.mb-5
      .col
        .btn-h3.mb-4
          span.h3 Costos de certificacin

        p.mb-4 La certificacin inicial incluye los costos tanto para implementar el SGSI como para certificar ISO/IEC 27001. El costo de implementacin depende en gran medida de las brechas entre los controles de seguridad existentes y los controles requeridos dentro de la organizacin. En trminos de costos de implementacin hay costos y recursos para implementar controles de seguridad, redaccin de documentacin, capacitacin del personal, etc. La certificacin en s, incluye el costo de los auditores externos (que cobran una tarifa determinada por da), tarifas de solicitud, tasas de certificados y tasas de mantenimiento.

        .btn-h3.mb-4
          span.h3 Nivel de adopcin e implementacin

        p Segn ISO Survey (2020), haba unos 44.500 certificados ISO/IEC 27001 vlidos en 137 pases y economas de todo el mundo al 31 de diciembre de 2020. Los tres principales pases y economas por el nmero total de certificados vlidos eran China (unos 12.00), Japn (unos 5.600) y el Reino Unido de Gran Bretaa e Irlanda del Norte (unos 3.300). Segn la informacin de la misma encuesta, el nmero de certificados vlidos en Hong Kong era de unos 186, nmero que inclua algunos departamentos gubernamentales certificados segn ISO/IEC 27001 para reas funcionales especficas.

