    .btn-h3.mb-4
      span.h3 Descripcin general del proceso de implementacin y certificacin de ISO/IEC 27001

    p.mb-5 Revise en el siguiente paso a paso el detalle del proceso de implementacin y certificacin para ISO/IEC 27001:

