      span.h3 Familia de ISO/IEC 27000

    p.mb-5 Consta de normas y directrices interrelacionadas, ya publicadas o en desarrollo, y contiene una serie de componentes estructurales centrados en los estndares normativos que describen los requisitos del SGSI (ISO/IEC 27001), los requisitos de los organismos de certificacin (ISO/IEC 27006) para aquellos que certifican la conformidad con ISO/IEC 27001, y marco de requisitos adicionales para las implementaciones especficas del sector del SGSI (ISO/IEC 27009). Otros estndares y pautas brindan orientacin para varios aspectos de la implementacin de un SGSI, abordando un proceso genrico, as como orientacin especfica del sector. Ver tabla.

