    p.mb-5 Adems de las ms mencionadas ISO/IEC 27001, ISO/IEC 27002 e ISO/IEC 27018, tambin es importante hacer referencia a otras normas de la familia ISO/IEC 27000 como las que se destacan a continuacin:

    .row.justify-content-center.mb-5(data-aos="fade-right")
