          .row(titulo="ISO/IEC 27003")
            .col-lg-11
              p.font_text1.text-bold Sistemas de gestin de la seguridad de la informacin  Orientacin.
              p Brinda orientacin sobre los requisitos para un SGSI como se especifica en ISO/IEC 27001, as como las recomendaciones, posibilidades y permisos en relacin con los requisitos.
          .row(titulo="ISO/IEC 27004")
            .col-lg-11
              p.font_text1.text-bold Gestin de la seguridad de la informacin - Monitoreo, medicin, anlisis y evaluacin.
              p Proporciona pautas para ayudar a las organizaciones a evaluar el desempeo de la seguridad de la informacin, la efectividad de un SGSI y cumplir con los requisitos de monitoreo, medicin, anlisis y evaluacin como especifica la Norma ISO/IEC 27001.
          .row(titulo="ISO/IEC 27005")
            .col-lg-11
              p.font_text1.text-bold Gestin de riesgos de seguridad de la informacin.
              p Ofrece directrices para la gestin de riesgos de seguridad de la informacin. Esta norma es compatible con los conceptos generales especificados en ISO/IEC 27001 y est diseada para ayudar a la implementacin satisfactoria de la seguridad de la informacin basada en un enfoque de gestin de riesgos.
          .row(titulo="ISO/IEC 27017")
            .col-lg-11
              p.font_text1.text-bold Cdigo de prcticas para controles de seguridad de la informacin basado en ISO/IEC 27002 para servicios en la nube.
              p Proporciona pautas que respaldan la implementacin de controles de seguridad de la informacin para consumidores y proveedores de servicios en la nube. La seleccin de los controles apropiados y la aplicacin de la gua de implementacin se basa en la evaluacin de riesgos y otros requisitos para el uso de servicios en la nube.
          .row(titulo="ISO/IEC 27031")
            .col-lg-11
