              p Describe los conceptos y principios de la preparacin de las Tecnologas de la Informacin y la Comunicacin (TIC) para la continuidad del negocio, y proporciona un marco de mtodos y procesos para identificar y especificar todos los aspectos para mejorar la preparacin TIC de una organizacin y garantizar la continuidad del negocio.
