              p Proporciona conceptos bsicos y fases de la gestin de incidentes de seguridad de la informacin, y combina estos conceptos con principios en un enfoque estructurado para detectar, informar, evaluar y responder a los incidentes y aplicar las lecciones aprendidas.
          .row(titulo="ISO/IEC 27035-2")
            .col-lg-11
              p.font_text1.text-bold Gestin de incidentes de seguridad de la informacin - Parte 2: Pautas para planificar y prepararse para la respuesta a incidentes.
