              p Define las pautas que respaldan la implementacin de SGSI para el uso de servicios en la nube.
          .row(titulo="ISO/IEC 27037")
            .col-lg-11
              p.font_text1.text-bold Pautas para la identificacin, recopilacin, adquisicin y conservacin de evidencia digital.
              p Proporciona pautas para actividades especficas en el manejo de evidencia digital: identificacin, recopilacin, adquisicin y preservacin de evidencia digital potencial que puede tener valor probatorio.
          .row(titulo="ISO/IEC 27039")
            .col-lg-11
              p.font_text1.text-bold Seleccin, implementacin y operaciones de sistemas de prevencin y deteccin de intrusos (IDPS)
              p Brinda pautas para ayudar a las organizaciones a prepararse para implementar IDPS, en particular, aborda la seleccin, el despliegue y las operaciones de IDPS.
          .row(titulo="ISO/IEC 27043")
            .col-lg-11
              p.font_text1.text-bold Principios y procesos de investigacin de incidentes.
              p Ofrece pautas basadas en modelos idealizados para procesos comunes de investigacin de incidentes en varios escenarios que involucran evidencia digital. Incluye procesos que van desde la preparacin previa al incidente hasta el cierre de la investigacin, as como consejos generales y advertencias sobre dichos procesos.
          .row(titulo="ISO/IEC TS 27110")
            .col-lg-11
              p.font_text1.text-bold Pautas de desarrollo del marco de ciberseguridad.
              p Especifica las pautas para desarrollar un marco de ciberseguridad, independientemente del tipo, tamao o naturaleza de las organizaciones.
          .row(titulo="ISO/IEC TS 27570")
            .col-lg-11
              p.font_text1.text-bold Directrices de privacidad para ciudades inteligentes.
              p Brinda orientacin sobre la proteccin de la privacidad del ecosistema de ciudades inteligentes: cmo se pueden utilizar las normas a nivel mundial y a nivel organizativo en beneficio de los ciudadanos y procesos para la proteccin de la privacidad del ecosistema de la ciudad inteligente.

    .btn-h3.mb-4
      span.h3 Informacin de identificacin personal (PII) en <em>Cloud Computing </em>

    .row.mb-4.mb-lg-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img.img-t.mx-auto(src='@/assets/curso/tema2/img30.png', alt='')
      .col-lg-8(data-aos="fade-left")
        p La computacin en la nube est evolucionando como nunca antes con tendencia al crecimiento y al desarrollo en los prximos aos. Es bien sabido que la computacin en la nube tiene ventajas potenciales como mtodo ms rentable para usar, mantener y actualizar. El mtodo de copia de seguridad y recuperacin en la computacin en la nube es relativamente ms fcil que los mtodos tradicionales de almacenamiento de datos. Adems, ofrece la ventaja de una implementacin rpida y un fcil acceso a la informacin.
        p Desde la perspectiva de las organizaciones, la seguridad de la computacin en la nube es motivo de gran preocupacin, especialmente en cuestiones de seguridad de datos y proteccin de la privacidad, lo que constituye el principal inhibidor para la adopcin de este tipo de servicio.

    .bg_35
      .row.mb-5
        .col-lg-10(data-aos="fade-right")
          p El estndar ISO/IEC 27018:2019 (en adelante, ISO/IEC 27018) conocido como <b>"Cdigo de prcticas para la proteccin de informacin de identificacin personal (PII) en nubes pblicas que actan como procesadores de PII,</b> es el primer estndar internacional centrado en la proteccin de datos personales en la nube pblica. ISO/ IEC 27018 establece principalmente objetivos de control, controles y pautas comnmente aceptados en relacin con la proteccin de PII que procesan los proveedores de servicios de la nube pblica, es decir, procesadores de PII.
          p ISO/IEC 27018 ha sido diseado para todo tipo y tamao de organizacin en el sector pblico y privado que brinde servicios de procesamiento de informacin a travs de la nube como procesadores de PII.
        .col-lg-2
          .row.justify-content-center
            .col-auto
              figure(data-aos="fade-down-right")
                img.img-t.m-auto(src='@/assets/curso/tema2/img12.svg', alt='')

    .btn-h3.mb-4
      span.h3 Controles de proteccin de PII de ISO/IEC 27018

    p <b>ISO/IEC 27018</b> se desarroll teniendo en cuenta los requisitos ya incluidos en ISO/IEC 27002. <b>Aumenta ISO/ IEC 27002 en dos enfoques:</b> en primer lugar, complementa la gua de implementacin para los controles prescritos por ISO/IEC 27002 y en segundo lugar, proporciona controles adicionales y orientacin asociada que se adapta para abordar los requisitos de proteccin de PII de la nube pblica que no estn cubiertos por el conjunto de controles ISO/IEC 27002. Para el primer enfoque, ISO/IEC 27018 proporciona una gua de implementacin adicional sobre los siguientes 11 controles de ISO/IEC 27002:

    .bg_37.p-4.mb-4
      .row.justify-content-around
        .col-lg-4.align-self-center.mb-4.mb-lg-0(data-aos="fade-right")
          figure(data-aos="fade-down-right")
            img.img-t.m-auto(src='@/assets/curso/tema2/img33.svg', alt='')
        .col-lg-7(data-aos="fade-left")
          ul.lista-ul--color
            li
              i.fas.fa-check-circle
              span Polticas de seguridad de la informacin.
            li
              i.fas.fa-check-circle
              span Organizacin de la seguridad de la informacin.
            li
              i.fas.fa-check-circle
              span Seguridad de los recursos humanos.
            li
              i.fas.fa-check-circle
              span Control de acceso.
            li
              i.fas.fa-check-circle
              span Criptografa.
            li
              i.fas.fa-check-circle
              span Seguridad fsica y ambiental.
            li
              i.fas.fa-check-circle
              span Seguridad de las operaciones.
            li
              i.fas.fa-check-circle
              span Seguridad de las comunicaciones.
            li
              i.fas.fa-check-circle
              span Gestin de incidentes de seguridad de la informacin.
            li
              i.fas.fa-check-circle
              span Aspectos de seguridad de la informacin de la gestin de la continuidad del negocio.
            li
              i.fas.fa-check-circle
              span Cumplimiento.

    p.mb-5 Para el segundo enfoque, el Anexo A de ISO/IEC 27018 enumera 11 controles extendidos de ISO/IEC 27002 para cumplir con los requisitos de proteccin de PII que se aplican a los proveedores de servicios de la nube pblica que actan como procesadores de PII. Estos controles ampliados se clasifican segn los 11 principios de privacidad de la Norma ISO/IEC 29100:2011 (en lo sucesivo, ISO/IEC 29100), conocida como "Tecnologa de la informacin - Tcnicas de seguridad - Marco de privacidad".

    .btn-h3.mb-4
      span.h3 Beneficios de ISO/IEC 27018

    p ISO/IEC 27018 se aplica al procesamiento de PII obtenida de un cliente para los fines determinados por el cliente en virtud de su contrato con el proveedor de servicios en la nube.
    p Al adoptar ISO/IEC 27018 una organizacin puede:

    .row.align-items-center.mb-0.mb-lg-4
      .col-lg-6(data-aos="fade-right")
        .bg_39.p-4
          .row.align-items-center
            .col-auto
              img.img-t.m-auto(src='@/assets/curso/tema2/img36.svg', alt='')
            .col-lg-9.col-md-8.col-8
              p.m-0 Establecer una gua para facilitar el cumplimiento de los requisitos de proteccin de datos relevantes.
      .col-lg-6(data-aos="fade-left")
        .bg_39.p-4
          .row.align-items-center
            .col-auto
              img.img-t.m-auto(src='@/assets/curso/tema2/img37.svg', alt='')
            .col-lg-9.col-md-8.col-8
              p.m-0 Ganar la confianza de los clientes para dejar sus datos en la nube y as ampliar su base de clientes.

    .row.justify-content-center.mb-4.mb-lg-5
      .col-lg-7.col-md-12(data-aos="fade-up")
        .bg_39-1.p-4.p-xl-3.p-xxl-4
          .row.align-items-center
            .col-auto
              img.img-t.m-auto(src='@/assets/curso/tema2/img38.svg', alt='')
            .col-lg-10.col-xl-9.col-xxl-10.col-md-8.col-8
              p.m-0 Ayudar al proveedor de servicios de nube pblica, que opera en un mercado multinacional, a cumplir con varios estndares nacionales de proteccin de datos y realizar evaluaciones complejas en cada jurisdiccin.

    p <b>ISO/IEC 27001</b> establece una especificacin formal para ISMS, con mucho nfasis en el <b>"sistema de gestin"</b> en lugar de la <b>"seguridad de la informacin"</b>.  Un SGSI certificado proporciona una fuerte indicacin de que una organizacin est utilizando un enfoque sistemtico para la identificacin, evaluacin y gestin de los riesgos de seguridad de la informacin. Si hay un SGSI efectivo en funcionamiento, entonces, el SGSI garantizar que existan controles de seguridad adecuados. El certificado ISO/IEC 27001 tiene potencial de #[em marketing] y debe ayudar a mejorar la credibilidad y aumentar la confianza del cliente.

    Separador

    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="zoom-out-right")
      h2 2.2 ISO 27002

    .row.mb-4.mb-lg-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img.img-t.mx-auto(src='@/assets/curso/tema2/img39.png', alt='')
      .col-lg-8(data-aos="fade-left")
        p El estndar internacional ISO/IEC 27002 hace referencia a la seguridad de la informacin en las empresas y organizaciones, con el principal objetivo de reducir el riesgo al mximo, evitando alteraciones en la informacin ya sea por robo, destruccin o prdida.
        p La reduccin del riesgo es un tema relevante que involucra el desarrollo de procesos y el seguimiento de medidas adecuadas y eficientes para la empresa en consideracin a los requerimientos y las restricciones estipuladas por la legislacin nacional e internacional.  Tambin es necesario que la organizacin tenga claridad en cuanto a sus objetivos, el bienestar de sus usuarios, ya sean clientes o empleados y los costos necesarios para la implementacin y operacin, no sea que incurra en medidas extremadamente costosas que estn por encima o afecten la rentabilidad de la compaa.
        .bg_40.p-4
          .row.justify-content-around.align-items-center
            .col-lg-9
              p.m-0 No se pueden establecer controles para una seguridad total, <b>pero s se puede conseguir la reduccin del riesgo al mnimo,</b> que en caso de materializarse alguna amenaza, los daos sean menores y fciles de restaurar.
            .col-auto
              figure
                img.img-t.mx-auto(src='@/assets/curso/tema2/img40.svg', alt='')

    p Explore algunas categoras de seguridad incluidas en el estndar internacional ISO/IEC 27002:

    .row.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-12.col-md-8
        SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img41.png' alt="")
            h4.text-white Poltica de seguridad
            p.text-white Cada organizacin debe crear su propio documento de polticas de seguridad de la informacin, que debe planificarse teniendo en cuenta tanto los requerimientos comerciales propios del negocio, como las leyes y las regulaciones que le apliquen.
            p.text-white El documento en primera instancia debe ser aprobado por la gerencia y luego socializado a todos los departamentos, jefes, empleados y externos importantes para la compaa.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img42.png' alt="")
            h4.text-white Aspectos organizativos de la seguridad de la informacin
            p.text-white Manejo de la seguridad de la informacin al interior de la organizacin y con respecto a terceros, identificando las actividades que involucren ingreso, modificacin, manipulacin e intercambio de informacin entre las partes.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img43.png' alt="")
            h4.text-white Gestin de activos
            p.text-white A cada activo dentro de la empresa se le debe asociar o asignar un responsable, tambin se precisa mantener el inventario permanentemente actualizado con todos los activos, su manejo correcto, clasificacin y a quin pertenece.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img44.png' alt="")
            h4.text-white Seguridad ligada a los recursos humanos
            p.text-white Se debe garantizar que empleados, contratistas y grupos externos comprendan sus responsabilidades, valorando la idoneidad en las tareas delegadas, lo que contribuye a la reduccin de riesgos como robo, fraude o mala manipulacin de activos informticos.
          .tarjeta.bg_41.p-4.p-xl-3.p-xxl-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img45.png' alt="")
            h4.text-white Seguridad fsica y ambiental
            p.text-white Proteccin fsica en la que se tengan barreras o lmites de proteccin (paredes, puertas de acceso con control inteligente o vigilancia privada) para salvaguardar las reas en donde se almacena y procesa la informacin.
            p.text-white Tambin se deben considerar amenazas externas o causadas ambientalmente (incendios, inundaciones, huracanes, terremotos, ataques terroristas) y en cuanto al ambiente (temperatura ideal, seguridad para el cableado, mantenimiento).
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img46.png' alt="")
            h4.text-white Gestin de comunicaciones y operaciones
            p.text-white Orientada a garantizar la operacin funcional y segura de los dispositivos encargados de procesar la informacin.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img47.png' alt="")
            h4.text-white Control de acceso
            p.text-white Estos controles se realizan a travs del registro de usuario, validacin de roles y privilegios, autenticacin, denegacin y activacin de usuarios, entre otros.
            p.text-white En cuanto a los accesos desde dispositivos mviles, conexin a la red inalmbrica, acceso desde computadores porttiles o teletrabajo para personal que realice actividades desde casa se deben establecer unas polticas claras detallando cada uno de estos aspectos.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img48.png' alt="")
            h4.text-white Adquisicin, desarrollo y mantenimiento de los sistemas de informacin
            p.text-white Adems de las consideraciones en calidad y valor, se debe estudiar la seguridad que proporcionan nuevos equipos y sistemas, con validaciones de entrada y salida de los datos, controles internos del procesamiento de las aplicaciones, y verificacin de la integridad de los mensajes.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img49.png' alt="")
            h4.text-white Gestin de incidentes en la seguridad de la informacin
            p.text-white Los eventos y las debilidades que se presenten en la seguridad de los activos de informacin se deben documentar y crear informes que contribuyan a mantener una comunicacin a tiempo, garantizando que las correcciones se apliquen oportunamente.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img50.png' alt="")
            h4.text-white Gestin de la continuidad del negocio
            p.text-white Los desastres que puedan provocar las fallas en la implementacin de la seguridad, cadas o perdidas del servicio se deben validar con respecto al impacto en la operacin comercial del negocio. En caso de desastres, contar con un plan de accin que indique la metodologa con la que el negocio reanudar sus operaciones.
          .tarjeta.bg_41.p-4
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img51.png' alt="")
            h4.text-white Cumplimiento
            p.text-white Cumplir con los requerimientos legales debe ser una de las prioridades de toda organizacin, evitando incurrir en violaciones de la ley.

    .bg_42.p-4
      p A manera de buenas prcticas se recomiendan las siguientes acciones para las categoras de seguridad:
      .bg_43.py-4.mb-5
        .row.justify-content-around.align-items-center
          .col-lg-3(data-aos="fade-right")
            figure.mb-5
              img.img-t.m-auto(src='@/assets/curso/tema2/img53.svg' alt="")
          .col-lg-9.col-md-8(data-aos="fade-left")
            SlyderF.mb-5(columnas="col-lg-6 col-xl-6")
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img54.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters A.
                p Cada procedimiento en las operaciones debe estar debidamente documentado, no en la memoria de los administradores, as como el control de cambios realizado con previa autorizacin y posterior anlisis de las mejoras alcanzadas.  Nadie debe tener acceso a realizar modificaciones o a hacer uso de activos informticos sin previa autorizacin.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img55.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters2 B.
                p Crear entornos separados: uno para ambiente de pruebas y operacin, y otro para el entorno desarrollo, evitando posibles problemas operacionales.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img56.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters3 C.
                p Las empresas dedicadas a la venta de servicios deben realizar la implementacin y conservar niveles adecuados de seguridad informtica y asegurar que los servicios que se dispongan en lnea cuenten con los acuerdos y compromisos correspondientes a este tipo de entrega a grupos externos.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img57.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters D.
                p Proteger contra los cdigos maliciosos y los virus que se puedan descargar e instalar, implementando las medidas de deteccin, prevencin y recuperacin (implementacin de antivirus con garanta de actualizacin).
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img58.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters E.
                p Se debe mantener un plan de copias de seguridad eficiente, automatizado, con revisiones continuas a la integridad de la informacin, evitando grandes prdidas de datos en la compaa y serios problemas en la operacin.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img59.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters F.
                p Cuando se trate de redes, garantizar la proteccin de los datos transportados, adems del aseguramiento de la infraestructura. Los servicios que se ofrecen en la red, igualmente deben ser seguros. Todos los activos informticos y de red deben estar documentados, detallando cada elemento que la conforma.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img60.svg')
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters2 G.
                p El procedimiento para realizar los controles para intercambiar informacin a travs de las herramientas y medios para la comunicacin deben estar establecidos mediante polticas claras.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img61.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters3 H.
                p Para realizar un despliegue del comercio electrnico se debe garantizar la efectiva proteccin, blindndose de ataques fraudulentos, divulgacin de informacin no autorizada, alteracin de la informacin y dems problemas propios de exponer informacin en redes pblicas.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img62.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters I.
                p Monitorear permanentemente los sistemas que permitan detectar las actividades que no estn autorizadas, para ello tambin es importante contar con las auditoras necesarias.
              .tarjeta.bg_45.p-4
                .row.mb-4
                  .col-auto
                    figure
                      img.img-t(src='@/assets/curso/tema2/img63.svg' alt="")
                  .col-2.col-lg-1.align-self-end
                    h4.m_letters J.
                p De presentarse fallas debern ser corregidas rpidamente y documentarse para posteriores anlisis, que ayuden a la toma de medidas y acciones necesarias.

      h4(data-aos="fade-right") Adquisicin y mantenimiento de sistemas de informacin
      p En cuanto a los procesos de adquisicin, desarrollo y mantenimiento de los sistemas de informacin es importante tener en cuenta los siguientes aspectos:

      .row.mb-4
        .col-lg-12
          .bg_46.p-4.d-none.d-lg-block
            figure(data-aos="fade-down-right")
              img.img-t.m-auto.d-none.d-lg-block(src='@/assets/curso/tema2/img65.svg', alt='La imagen presenta los aspectos a tener en cuenta en cuanto a los procesos de adquisicin, desarrollo y mantenimiento de los sistemas de informacin')
      .row.justify-content-center.mb-4
        .col-12.d-lg-none.d-md-block
            figure(data-aos="fade-down-right")
              img.img-t.m-auto.d-lg-none.d-md-block(src='@/assets/curso/tema2/img66.svg', alt='La imagen presenta los aspectos a tener en cuenta en cuanto a los procesos de adquisicin, desarrollo y mantenimiento de los sistemas de informacin')

      h4(data-aos="fade-right") Cumplimiento  ISO/IEC 27002
      p Y para la categora de seguridad de cumplimiento, incluida en el estndar ISO/IEC 27002, se advierten las siguientes consideraciones.

      .row.mb-4.mb-md-0.mb-lg-5(data-aos="fade-right")
        .col-sm-6.col-xl-3.mb-4.mb-xl-0
          .tarjeta.tarjeta-flip.color-acento-botones(@mouseover="indicadorTarjetaFlip = false")
            .indicador--hover(v-if="indicadorTarjetaFlip")
            .tarjeta-flip__contenedor
              .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img68.png')})`}")
              .tarjeta-flip__contenido.bg_47.p-4.p-xl-3
                p.text-center.flex-center-text Tener claridad sobre las regulaciones de ley aplicables.

        .col-sm-6.col-xl-3.mb-4.mb-xl-0
          .tarjeta.tarjeta-flip.color-acento-botones(@mouseover="indicadorTarjetaFlip = false")
            .tarjeta-flip__contenedor
              .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img69.png')})`}")
              .tarjeta-flip__contenido.bg_47.p-4.p-xl-3
                p.text-center.flex-center-text Definir con precisin, realizar la documentacin y mantener actualizado todo lo necesario para el cumplimiento legal de cada uno de los sistemas informticos y en general de toda la empresa.

        .col-sm-6.col-xl-3.mb-4.mb-xl-0
          .tarjeta.tarjeta-flip.color-acento-botones(@mouseover="indicadorTarjetaFlip = false")
            .tarjeta-flip__contenedor
